import React, { useState } from 'react';
import { GoogleLogin } from 'react-google-login';
import axios from 'axios';
import styled from 'styled-components';
import Switch from "react-switch";
import Donate from './donate.png';
import BarLoader from "react-spinners/BarLoader";

const Container = styled.div`
  padding: 40px;
`;

const Block = styled.div`
  margin-bottom: 50px;
`;
const Title = styled.div`
  font-weight: 500;
  font-size: 1.2rem;
  margin-bottom: 16px;
`;

const Label = styled.div`
  display: flex;
  align-items: center;
`;

const Button = styled.button`

`;

const LoadingContainer = styled.div`
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
`;

const CLIENT_ID = '874134398713-9lotcehg8rm0d68ht0m2brdagp4gjgo9.apps.googleusercontent.com';

function App() {
  const [isLogin, setLogin] = useState(false);
  const [user, setUser] = useState({});
  const [loading, setLoading] = useState(false);
  const [enableWeekday, setWeekday] = useState(0);
  const [enableHoliday, setHoliday] = useState(0);

  const onSuccess = async ({ code }) => {
    setLoading(true);
    console.log('onSuccess', code);
    const { data } = await axios.post('https://create-tokens-apis-gateway-b5kl1urd.an.gateway.dev/create-tokens', { code });
    console.log('data', data);

    setLoading(false);
    if (data.success) {
      setLogin(true);
      setUser({ email: data.data.email, name: data.data.name });
      setWeekday(data.data.enable_weekday);
      setHoliday(data.data.enable_holiday);
    }
  };

  const onFailure = (err) => {
    console.log('onFailure', err);
    alert(err);
  };

  const updateConfigs = async (config) => {
    setLoading(true);
    const { data } = await axios.post('https://update-configs-apis-gateway-b5kl1urd.an.gateway.dev/update-configs', {
      email: user.email,
      enable_weekday: enableWeekday,
      enable_holiday: enableHoliday,
      ...config
    });

    setLoading(false);
    console.log('data', data);
    setWeekday(data.enable_weekday);
    setHoliday(data.enable_holiday);
  }

  const onChangeWeekday = (checked) => {
    updateConfigs({ enable_weekday: checked ? 1 : 0 });
  };

  const onChangeHoliday = (checked) => {
    updateConfigs({ enable_holiday: checked ? 1 : 0 });
  };

  return (
    <Container>
      {loading && <LoadingContainer>
        <BarLoader color="rgb(54, 183, 215)" loading={loading} width="100%" />
      </LoadingContainer>}
      <Block>
        <Title>1. gmail10</Title>
      </Block>
      <Block>
        <Title>2. </Title>
        {!isLogin && <>
          <GoogleLogin
            clientId={CLIENT_ID}
            buttonText=""
            onSuccess={onSuccess}
            onFailure={onFailure}
            cookiePolicy='single_host_origin'
            responseType='code'
            accessType='offline'
            scope='openid email profile https://www.googleapis.com/auth/calendar'
          />
          <p>LINE</p>
        </>}
      </Block>
      {!isLogin ?
        <Block>
          <Title>3. </Title>
        </Block>
        :
        <>
          <Block>
            <Title>3. {user.name} {user.email}</Title>
            <p>()</p>
            <p>06:0018:0035.5~37</p>
            <p></p>
            <p></p>
            <Label>
              <span></span>
              <Switch onChange={onChangeWeekday} checked={enableWeekday} />
            </Label>
            <br />
            <Label>
              <span></span>
              <Switch onChange={onChangeHoliday} checked={enableHoliday} />
            </Label>
          </Block>
          <Block>
            <Title>4. </Title>
            <Button></Button>
            {/* <p></p>
            <p>3QQ</p>
            <p></p> */}
          </Block>
          <Block>
            <Title>5. 10</Title>
            <a href="https://i.loli.net/2019/07/15/5d2c29a98af0648789.gif" target="_blank"><img src={Donate} width="30%"></img></a>
          </Block>
        </>
      }
    </Container>
  );
}

export default App;
